body {
  background: #f5f5f5;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.app-container {
  max-width: 1200px;
  margin: auto;
  padding: 1rem;
}

header {
  text-align: center;
  margin-bottom: 2rem;
}

h1 {
  color: #333;
  margin: 0;
}

section {
  background: #fff;
  border-radius: 8px;
  margin-bottom: 2rem;
  padding: 1rem;
  box-shadow: 0 2px 4px #0000001a;
}

form {
  flex-wrap: wrap;
  gap: .5rem;
  margin-bottom: 1rem;
  display: flex;
}

form input, form textarea, form select {
  border: 1px solid #ddd;
  border-radius: 4px;
  flex: 1;
  padding: .5rem;
}

form button {
  color: #fff;
  cursor: pointer;
  background: #07c;
  border: none;
  border-radius: 4px;
  padding: .5rem 1rem;
}

form button:hover {
  background: #005fa3;
}

.members-section ul {
  padding: 0;
  list-style: none;
}

.members-section li {
  border-bottom: 1px solid #eee;
  padding: .3rem 0;
}

.filters {
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
  display: flex;
}

.filter-buttons {
  margin-top: .5rem;
}

.board {
  flex-wrap: wrap;
  gap: 1rem;
  display: flex;
}

.column {
  background: #fafafa;
  border-radius: 8px;
  flex: 1;
  min-height: 300px;
  padding: 1rem;
  box-shadow: 0 1px 3px #0000000d;
}

.column h2 {
  text-align: center;
  color: #444;
  margin-top: 0;
  margin-bottom: 1rem;
}

.task-card {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 6px;
  margin-bottom: 1rem;
  padding: .8rem;
  transition: transform .1s, box-shadow .2s;
  box-shadow: 0 1px 3px #0000001a;
}

.task-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 8px #00000026;
}

.task-card h3 {
  margin-top: 0;
  margin-bottom: .3rem;
  font-size: 1.1rem;
}

.task-card p {
  margin: .2rem 0;
  font-size: .9rem;
}

.task-card label {
  margin-top: .5rem;
  font-size: .85rem;
  display: block;
}

.task-card select {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin-top: .2rem;
  padding: .3rem;
}

.task-card button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
  margin-top: .5rem;
  margin-right: .5rem;
  padding: .4rem .8rem;
  font-size: .9rem;
}

.task-card .done-btn {
  color: #fff;
  background: #28a745;
}

.task-card .done-btn:disabled {
  cursor: not-allowed;
  background: #aaa;
}

.task-card .delete-btn {
  color: #fff;
  background: #dc3545;
}

.task-card[data-category="ux"] {
  border-left: 5px solid #9b59b6;
}

.task-card[data-category="frontend"] {
  border-left: 5px solid #3498db;
}

.task-card[data-category="backend"] {
  border-left: 5px solid #2ecc71;
}

@media (width <= 768px) {
  .board {
    flex-direction: column;
  }
}
